/* Centrado total de la sección */
.ceremonyfiesta-section {
  background: transparent !important; /* deja ver el fondo aplicado a toda la sección */
  border-radius: 16px;
  box-shadow: none; /* sin sombra para que el fondo sea protagonista */
  border: none;
  margin: 2rem auto;
  padding: 2rem 1.5rem;
  max-width: 700px;
  min-height: unset;
  width: min(700px, 92vw);
  display: block;
  box-sizing: border-box;
  position: relative;
  overflow-x: visible;
}

.ceremonyfiesta-content-centered {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
    max-width: 500px;
    width: 100%;
  box-sizing: border-box;
}
.ceremonyfiesta-img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  border: 4px solid var(--border);
  box-shadow: 0 2px 16px rgba(191,169,128,0.10);
  display: block;
  margin: 0 auto 1.2rem auto;
  background: #fff;
}

.ceremonyfiesta-img-round {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  display: block;
  margin: 0 auto 1.2rem auto;
}
.ceremonyfiesta-icon {
  margin-bottom: 0.5rem;
}
.ceremonyfiesta-title {
  font-family: 'Dantina', sans-serif;
  font-size: 2.2rem;
  color: var(--text);
  text-align: center;
  margin: 0.5rem 0 1.2rem 0;
  font-weight: 400;
  letter-spacing: 0.04em;
}
/* Eliminado duplicado ceremonyfiesta-img */
.ceremonyfiesta-label {
  font-size: 0.9rem;
  color: var(--text-muted);
  letter-spacing: 0.1em;
  margin-top: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
  font-family: 'Playfair Display', serif;
  text-align: center;
}
.ceremonyfiesta-date, .ceremonyfiesta-place {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.ceremonyfiesta-place {
  font-size: 1rem;
  color: var(--text-muted);
  margin-bottom: 0.5rem;
  text-align: center;
}
.ceremonyfiesta-buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1.5rem;
  flex-wrap: wrap;
}
.ceremonyfiesta-btn {
  background: #fff;
  border: 1.5px solid var(--primary);
  color: var(--primary);
  border-radius: 28px;
  padding: 0.6rem 1.2rem;
  min-height: 44px; /* touch target */
  font-size: 0.95rem;
  letter-spacing: 0.04em;
  text-transform: none;
  font-family: 'Roboto', sans-serif;
  cursor: pointer;
  text-decoration: none;
  transition: background 0.2s, color 0.2s, border-color 0.2s, box-shadow 0.2s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  text-align: center;
  flex: 1 1 260px; /* mismo ancho base para 2 por fila cuando quepa */
  max-width: 420px;
}
.ceremonyfiesta-btn:hover {
  background: var(--primary);
  color: #fff;
  box-shadow: 0 4px 20px rgba(0,0,0,0.08);
}

/* Fondo de toda la sección de ceremonia (envolvente) */
.ceremonyfiesta-bg {
  width: 100%;
  /* Full-bleed background container with comfortable spacing */
  padding: 4rem 0; /* make background clearly visible around the card */
  min-height: 60vh; /* treat it like a full section */
  background-color: transparent;
  background-attachment: scroll; /* safer on mobile */
  position: relative;
}

.ceremonyfiesta-bg::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px; /* overlap slightly to avoid gaps */
  height: 56px; /* wave height */
  background: url('../assets/wave-mid-brush.png') no-repeat 50% 100% / cover;
  pointer-events: none;
}

@media (max-width: 768px) {
  .ceremonyfiesta-bg {
    padding: 2rem 0;
    min-height: 50vh;
  }
}

@media (max-width: 480px) {
  .ceremonyfiesta-bg {
    padding: 1.5rem 0;
    min-height: 45vh;
  }
}

/* Responsive */
@media (max-width: 768px) {
  .ceremonyfiesta-img,
  .ceremonyfiesta-img-round {
    width: 160px;
    height: 160px;
  }
}

@media (max-width: 480px) {
  .ceremonyfiesta-img,
  .ceremonyfiesta-img-round {
    width: 140px;
    height: 140px;
  }
  .ceremonyfiesta-buttons {
  flex-direction: column;
  align-items: stretch;
  gap: 0.6rem;
  }
  .ceremonyfiesta-btn {
  width: 100%;
  justify-content: center;
  flex: 0 0 auto; /* evita que crezca en columna y se haga gigante */
  padding: 0.55rem 1rem; /* un poco menos de alto en mobile */
  }
}
