.counter-section {
  width: 100vw;
  height: 100vh;
  min-height: 100vh;
  /* Fondo a pantalla completa con la imagen fondoFull */
  background: var(--bg) url('../assets/fondoFull2.png') no-repeat 50% 50% / cover;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.counter-section::before {
  /* Velo sutil para mejorar legibilidad sobre la foto */
  content: '';
  position: absolute;
  inset: 0;
  background: rgba(249, 246, 239, 0.38); /* #f9f6ef con opacidad */
  pointer-events: none;
}
.counter-overlay {
  background: transparent;
  border-radius: 0;
  box-shadow: none;
  padding: 2rem 1rem 1.5rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 340px;
  width: 100%;
  position: relative;
  z-index: 1;
}
.counter-icon {
  width: 60px;
  opacity: 0.5;
  margin-bottom: 1.2rem;
}
.counter-date {
  font-size: 1.1rem;
  color: var(--text-muted);
  letter-spacing: 0.18em;
  margin-bottom: 0.7rem;
  margin-top: 0.5rem;
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 400;
  text-align: center;
  border-top: 1px solid var(--border);
  border-bottom: 1px solid var(--border);
  padding: 0.3rem 0;
  width: 100%;
}
.counter-title {
  font-family: 'Dantina', sans-serif;
  font-size: 2rem;
  color: var(--text);
  text-align: center;
  margin: 0.5rem 0 0.5rem 0;
  font-weight: 400;
  letter-spacing: 0.02em;
  white-space: nowrap; /* evita salto de línea: "Flor Carlos" en una sola línea */
}
.counter-heart {
  color: #bfa980;
  font-size: 1.4rem;
  vertical-align: middle;
}
.counter-subtitle {
  font-size: 1rem;
  color: var(--text-muted);
  text-align: center;
  margin-bottom: 1.2rem;
  margin-top: 0.5rem;
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 400;
  letter-spacing: 0.1em;
  border-bottom: 1px solid var(--border);
  border-top: 1px solid var(--border);
  width: 100%;
  padding: 0.3rem 0;
}
.counter-label {
  font-size: 0.9rem;
  color: var(--text-muted);
  letter-spacing: 0.1em;
  margin-bottom: 0.7rem;
  text-transform: lowercase;
  text-align: center;
}
.counter-countdown {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 0.5rem;
}
.counter-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 60px;
}
.counter-number {
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 2rem;
  color: var(--text);
  font-weight: 400;
  margin-bottom: 0.2rem;
  letter-spacing: 0.04em;
}
.counter-text {
  font-size: 0.8rem;
  color: var(--text-muted);
  letter-spacing: 0.1em;
  text-transform: uppercase;
  font-family: 'Roboto', Arial, sans-serif;
}

/* Responsive */
@media (max-width: 768px) {
  .counter-section {
    /* En mobile usar la imagen vertical fondoCel */
    background: var(--bg) url('../assets/fondoCel.jpg') no-repeat 50% 50% / cover;
  }
  .counter-overlay { max-width: 90vw; }
  .counter-title { font-size: 1.8rem; }
  .counter-subtitle { font-size: 0.95rem; }
  .counter-countdown { gap: 1rem; }
  .counter-number { font-size: 1.7rem; }
  .counter-text { font-size: 0.75rem; }
}

@media (max-width: 480px) {
  .counter-title { font-size: 1.6rem; }
  .counter-date { font-size: 1rem; }
  .counter-subtitle { font-size: 0.9rem; }
  .counter-item { min-width: 52px; }
  .counter-number { font-size: 1.5rem; }
  .counter-text { font-size: 0.7rem; }
}
